.line {
  bottom:0;
  display: block;
  height: auto;
  left:0;
  position:absolute;
  right:0;
  top:0;
  width: auto;
}

.line svg {
  display: block;
  height: 100%;
  width: 100%;
}

.line svg line {
  stroke: #333;
  stroke-width: .1rem;
}

.line svg .election-day path {
  fill: rgba(255,255,255,.5);
  stroke: transparent;
  stroke-width: 0;
}

.line svg .election-day line {
  stroke: #333;
  stroke-dasharray: 2 2;
  stroke-width: .1rem;
}

.line svg .election-day line.election-day--marker {
  stroke-dasharray: 0;
}

.line svg .election-day .election-day--label {
  fill: #333;
  font-family: 'Nunito Sans', sans-serif; 
  font-size: 1.1rem;
  font-weight: 300;
  text-anchor: middle;
  text-transform: uppercase;
}

.line svg .x-axis--calendar text.calendar-label {
  fill: #333;
  font-family: 'Nunito Sans', sans-serif; 
  font-size: 1.1rem;
  font-weight: 300;
  text-anchor: middle;
  text-transform: uppercase;
}

.line svg .x-axis--calendar g:first-child text.calendar-label {
  text-anchor: start;
}

.line svg .x-axis--calendar g:last-child text.calendar-label {
  text-anchor: end;
}

.line svg .x-axis--calendar text.calendar-label {
  visibility:hidden;
  font-size: 1.1rem;
  font-weight: 300;
}

.line svg .x-axis--calendar g:last-child text.calendar-label,
.line svg .x-axis--calendar g:first-child text.calendar-label {
  visibility: visible;
}

.line svg .x-axis--calendar g:nth-child(2n) text.calendar-label {
  visibility: visible;
}

.line svg .time-markers .time-markers--arrow {
  fill: #333;
}

.line svg .time-markers .time-markers--line {
  stroke: #333;
  stroke-dasharray: 4 2;
  stroke-width: .1rem;
}

.line svg .time-markers .time-markers--stopper {
  stroke: #333;
  stroke-dasharray: 0;
  stroke-width: .1rem;
}

.line svg .time-markers .time-markers--label {
  alignment-baseline: middle;
  fill: #333;
  font-family: 'Nunito Sans', sans-serif; 
  font-size: 1rem;
  font-weight: 300;
  text-anchor: middle;
  text-transform: uppercase;
}

@media screen and (min-width:750px) {
  .line svg .x-axis--calendar text.calendar-label {
    visibility: visible;
  }
}

.line svg .series .series--serie path {
  fill: none;
  stroke: #99d8c9;
  stroke-dasharray: 0;
  stroke-width: .2rem;
}


