.article.milestone {
	padding:4rem 0 5rem 0;
}

	@media screen and (min-width:750px) {
		.article.milestone {
			-ms-flex:0 0 100%;
			    flex:0 0 100%;
			max-width:100%;
			min-width:100%;
		}
	}


.article.milestone::before,
.article.milestone::after {
	content:"~";
	font-family: 'Playfair Display', Serif;
	font-size:3rem;
	height:3rem;
	left:50%;
	line-height:3rem;
	position:absolute;
	top:0;
	-webkit-transform:translate3d(-50%,0,0);
	        transform:translate3d(-50%,0,0);
}

.article.milestone::after {
	bottom:0;
	top:auto;
}

.article.milestone header {
	padding:0 2rem;
}

	@media screen and (min-width:750px) {
		.article.milestone header {
			padding:0 4rem;
		}
	}

	@media screen and (min-width:1360px) {
		.article.milestone header {
			margin:0 auto 4rem auto;
			max-width:90rem;
		}
	}

.article.milestone header h2,
.article.milestone header h1 {
	text-align:center;
}

.article.milestone header h2 {
	line-height:2.5;
	margin:0;
	white-space: nowrap;
}

.article.milestone header h1 {
	font-size:2.6rem;
	font-weight:400;
	padding-right:0;
}

	@media screen and (min-width:750px) {
		.article.milestone header h1 {
			font-size:3rem;
		}
	}

	@media screen and (min-width:1360px) {
		.article.milestone header h1 {
			font-size:3.6rem;
		}
	}

.article.milestone .milestone-body {
	padding:0 4rem;
}

	@media screen and (min-width:1360px) {
		.article.milestone .milestone-body {
			-ms-flex-line-pack:stretch;
			    align-content:stretch;
			-ms-flex-align:start;
			    align-items:flex-start;
			display:-ms-flexbox;
			display:flex;
			-ms-flex-direction:row;
			    flex-direction:row;
			-ms-flex-wrap:nowrap;
			    flex-wrap:nowrap;
			-ms-flex-pack:start;
			    justify-content:flex-start;
			margin:0 auto;
			max-width:90rem;
			padding:0 1rem;
			
		}
		
		.article.milestone .milestone-body .milestone-text {
			-ms-flex-item-align:auto;
			    align-self:auto;
			-ms-flex:1 1 66.6666%;
			    flex:1 1 66.6666%;
			max-width:66.6666%;
			-ms-flex-order:1;
			    order:1;
			padding:0 3rem;
			
		}
		
		.article.milestone .milestone-body footer {
			-ms-flex-item-align:auto;
			    align-self:auto;
			-ms-flex:1 1 33.3333%;
			    flex:1 1 33.3333%;
			max-width:33.3333%;
			-ms-flex-order:2;
			    order:2;
			padding:0 3rem;
		}
	}




.article.milestone p a {
	font-style:italic;
	text-decoration: underline;
}

.article.milestone footer h4 {
	font-size:1.4rem;
	font-weight: 400;
	line-height:2.5;
	margin:0;
	text-align:center;
	text-transform: uppercase;
	white-space: nowrap;
}

	@media screen and (min-width:1360px) {
		.article.milestone footer h4 {
			margin-bottom:1rem;
			text-align: left;
		}
	}

.article.milestone footer ul {
	margin:0;
	padding:0;
	text-align: center;
}

	@media screen and (min-width:1360px) {
		.article.milestone footer ul {
			text-align:left;
		}
	}

.article.milestone footer ul li {
	margin:0 .5rem;
}

	@media screen and (min-width:1360px) {
		.article.milestone footer ul li {
			display:block;
			margin:0 0 1rem 0;
		}
	}

.article.milestone footer ul li::after {
	content:"";
	display:none;
}


