.chart-parliament {
	display:block;
	height:auto;
	width:auto;
}

	@media screen and (min-width:750px) {
		.chart-parliament {
			-ms-flex-line-pack:start;
			    align-content:flex-start;
			-ms-flex-align:start;
			    align-items:flex-start;
			display:-ms-flexbox;
			display:flex;
			-ms-flex-direction:row;
			    flex-direction:row;
			-ms-flex-wrap:wrap;
			    flex-wrap:wrap;
			-ms-flex-pack:start;
			    justify-content:flex-start;
		}		
	}
	
	.chart-parliament svg.chart-parliament-viz {
		display:block;
		height:15rem;
		width:100%;
	}
	
		@media screen and (min-width:750px) {
			.chart-parliament svg.chart-parliament-viz {
				-ms-flex-item-align:start;
				    align-self:flex-start;
				-ms-flex:1 1 50%;
				    flex:1 1 50%;
				height:20rem;
				margin-bottom:4rem;
				max-width:50%;
				-ms-flex-order:0;
				    order:0;
				padding:0 3rem;
			}
		}
	
	.chart-parliament svg.chart-parliament-viz .chart-parliament-arcs,
	.chart-parliament svg.chart-parliament-viz .chart-parliament-borders {
		-webkit-transform-origin:50% 100%;
		    -ms-transform-origin:50% 100%;
		        transform-origin:50% 100%;
		-webkit-transform: translate3d(50%,100%,0);
		        transform: translate3d(50%,100%,0);
	}
	
	.chart-parliament svg.chart-parliament-viz .chart-parliament-arc {
		stroke:#efefef;
		stroke-width:.2rem;
	}
	
	.chart-parliament svg.chart-parliament-viz .chart-parliament-border {
		stroke:#333;
		stroke-width:.2rem;
	}
	
	.chart-parliament svg.chart-parliament-viz .chart-parliament-base {
		stroke:#333;
		stroke-width:.4rem;
	}
	
	.chart-parliament svg.chart-parliament-viz .chart-parliament-destra	{
		fill:#000;
	}
	
	.chart-parliament svg.chart-parliament-viz .chart-parliament-centro-destra	{
		fill:#4A90E2;
	}
	
	.chart-parliament svg.chart-parliament-viz .chart-parliament-sinistra	{
		fill:#D0021B;
	}
	
	.chart-parliament svg.chart-parliament-viz .chart-parliament-centro-sinistra {
		fill:#F55F23;
	}
		
	.chart-parliament svg.chart-parliament-viz .chart-parliament-m5s	{
		fill:#F8E71C;
	}
	
	.chart-parliament .chart-parliament-legend {
		padding-top:2rem;
	}
	
		@media screen and (min-width:750px) {
			.chart-parliament .chart-parliament-legend {
				-ms-flex-item-align:end;
				    align-self:flex-end;
				-ms-flex:1 1 50%;
				    flex:1 1 50%;
				margin-bottom:4rem;
				max-width:50%;
				-ms-flex-order:0;
				    order:0;
				padding:0 3rem;
			}
		}
	
	.chart-parliament .chart-parliament-legend ul,
	.chart-parliament .chart-parliament-legend li {
		border:0;
		list-style: none;
		margin:0;
		padding:0;
	}
	
	.chart-parliament .chart-parliament-legend ul {
		text-align:center;
	}
	
		@media screen and (min-width:750px) {
			.chart-parliament .chart-parliament-legend ul {
				text-align:left;
			}
		}
	
	.chart-parliament .chart-parliament-legend li {
		color:#333;
		display:inline-block;
		font-size:1.1rem;
		letter-spacing: .1rem;
		padding:0 1.5rem;
	}
	
	.chart-parliament .chart-parliament-legend .chart-parliament-legend-item a {
		color:#333;
		font-size:1.1rem;
		padding-left:1.4rem;
		position:relative;
		text-decoration:none;
		text-transform: uppercase;
	}
	
	.chart-parliament .chart-parliament-legend .chart-parliament-legend-item a:hover,
	.chart-parliament .chart-parliament-legend .chart-parliament-legend-item a:active {
		text-decoration: underline;
	}
	
	.chart-parliament .chart-parliament-legend .chart-parliament-legend-item a::before {
		background:#eee;
		border-radius:0;
		content: "";
		display:block;
		height:.8rem;
		left:.5rem;
		position:absolute;
		top:50%;
		-webkit-transform: rotate(45deg) translate3d(-50%,-50%,0);
		        transform: rotate(45deg) translate3d(-50%,-50%,0);
		-webkit-transform-origin: 0 0;
		    -ms-transform-origin: 0 0;
		        transform-origin: 0 0;
		width:.8rem;
	}
	
	.chart-parliament .chart-parliament-legend .chart-parliament-legend-item.centro-sinistra a::before {
		background:#F55F23;
	}
	
	.chart-parliament .chart-parliament-legend .chart-parliament-legend-item.sinistra a::before {
		background:#D0021B;
	}
	
	.chart-parliament .chart-parliament-legend .chart-parliament-legend-item.destra a::before {
		background:#000;
	}
	
	.chart-parliament .chart-parliament-legend .chart-parliament-legend-item.centro-destra a::before {
		background:#4A90E2;
	}
	
	.chart-parliament .chart-parliament-legend .chart-parliament-legend-item.m5s a::before {
		background:#F8E71C;  
	}


