.spine {
  display: block;
  height: auto;
  position: relative;
  width: 100%;
}

.spine--header,
.spine--body {
  -ms-flex-align: stretch;
      align-items: stretch;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
}

.spine--header h2 {
  display: block;
  font-size: 1.2rem;
  font-weight: 300;
  height: 2.6rem;
  line-height: 1.2;
  margin: 0;
  overflow-wrap: break-word;
  padding-left: 2rem;
  text-align: left;
  text-transform: uppercase;
  word-break: break-word;
}

.spine--header .spine--section:first-child h2 {
  padding-left: 0;
  padding-right: 2rem;
  text-align: right;
}

.spine--section {
  -ms-flex-item-align: start;
      align-self: flex-start;
  display: block;
  -ms-flex: 0 0 50%;
      flex: 0 0 50%;
  min-height: 4rem;
  position: relative;
}

.spine--section:after {
  background: #006d2c;
  bottom:-1.5rem;
  content:'';
  height:auto;
  position: absolute;
  right: -.1rem;
  top:-1.5rem;
  width:.2rem;
}

.spine--section:last-child:after {
  left:-.1rem;
  right:auto;
}

.spine--section--bar {
  background: #99d8c9;
  color: #fff;
  display: block;
  height: 4rem;
  left: 0;
  line-height: 4rem;
  overflow: visible;
  padding: 0 1rem;
  position: absolute;
  text-align: right;
  top: 0;
}

.spine--section:first-child .spine--section--bar {
  background: #ccece6;
  left: auto;
  right: 0;
  text-align: left;
}

.spine--section--value {
  color: #fff;
  height: 4rem;
  left: 0;
  line-height: 4rem;
  padding: 0 2rem;
  position: absolute;
  top: 0;
  z-index:2;
}

footer.spine--description {
  color: #999;
  display: block;
  font-size: 1.2rem;
  padding: 4rem 4rem 0 4rem;
  text-align:center;
  width: 100%;
}

footer.spine--description:empty {
  display:none;
}

header.spine--title {
  display: block;
  font-family: 'Playfair Display', Serif;
  font-size: 1.6rem;
  font-style: italic;
  font-weight: 700;
  line-height: 1.5;
  margin: 0;
  padding: 0 2rem 4rem 2rem;
  overflow-wrap: break-word;
  text-align: center;
  text-transform: none;
  white-space: nowrap;
  width: 100%;
  word-break: break-word;
}


