html {
	font-size:62.5%;
}

body {
	background:#fefefe;
	border:0;
	color:#333;
	font-family: 'Nunito Sans', sans-serif;
	font-size:1.6rem;
	font-weight:300;
	margin: 0;
	padding: 6rem 0 0;
}

	.home body {
	/*	padding: 0; */
	}

	@media screen and (min-width:750px) {
		body {
			padding-top: 10rem;
		}
	}

	@media screen and (min-width:1200px) {
		body {
			padding-left: 25rem;
			padding-top: 20rem;
		}
	}

* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	text-rendering: optimizeLegibility;
}

.in {
	display:block;
	min-height:100%;
	position:relative;
	width:100%;
}


