.main-footer {
	display:block;
	font-size:1.2rem;
	margin:6rem 2rem 2rem 2rem;
	max-width:86rem;
	position:relative;
	text-align: center;
}

@media screen and (min-width:1020px) {
	.main-footer {
		margin-top:12rem;
		padding:4rem 2rem;
		text-align:left;
	}
	
	.main-footer::before {
		background:#333;
		content:"";
		display:block;
		height:.1rem;
		left:2rem;
		position:absolute;
		top:0;
		width:25%;
	}
}

.main-footer a {
	color:#333;
}


