.stats {
	max-width:86rem;
	padding:0;
}

	@media screen and (min-width:750px) {
		.stats {
			padding:0 2rem;
		}
	}

	@media screen and (min-width:1360px) {
		.stats {
			padding:0 3rem;
		}
	}
	
.stats hr {
	background: transparent;
	border: 0;
	color: transparent;
	margin-bottom:4rem;
}

	@media screen and (min-width:750px) {
		.stats hr {
			margin-bottom:6rem;
		}
	}

.stats .stats-title {
	display: block;
	font-family: 'Playfair Display', Serif;
	font-size: 1.8rem;
	font-style: italic;
	font-weight: 700;
	line-height:1.5;
	margin:0 0 2rem 0;
	overflow-wrap: break-word;
	padding-right:4rem;
	text-transform: none;
	word-break: break-word;
}

	@media screen and (min-width:750px) {
		.stats .stats-title {
			font-size:2rem;
		}
	}
	
.stats .block {
	margin-bottom:4rem;
}

	@media screen and (min-width:750px) {
		.stats .block {
			margin-bottom:6rem;
		}
	}

.stats .block:last-child {
	margin-bottom:0;
}

.stats .block p a {
	color:#333;
	text-align: center;
	text-decoration: underline;
}

.stats .block h2 {
	font-family:'Zilla Slab', Cursive;
	font-size:1.6rem;
	font-style:normal;
	font-weight:700;
	letter-spacing:.2rem;
	line-height:1.4;
	padding-right:0;
	text-align: center;
	text-transform: uppercase;
}

	.stats .block h2::after,
	.stats .block h2::before {
		content:"—";
	}

	@media screen and (min-width:750px) {
		.stats .block h2 {
			font-size:2.6rem;
		}
	}
	
.stats .block h3 {
	color:#333;
	display: block;
	font-family: 'Playfair Display', Serif;
	font-size: 1.6rem;
	font-style: italic;
	font-weight: 400;
	line-height:1.5;
	margin:0 0 2rem 0;
	overflow-wrap: break-word;
	padding-right:4rem;
	text-transform: none;
	word-break: break-word;
}

	@media screen and (min-width:750px) {
		.stats .block h2 {
			font-size:1.8rem;
		}
	}

.stats .block .method {
	background:#efefef;
	padding:2rem;
}

.stats .block .method h4 {
	font-size:1.2rem;
	margin:0;
	text-transform: uppercase;
}

.stats .block .method p {
	font-size:1.4rem;
	margin-top:0;
}

.block-chart .chart {
	display:block;
	margin-bottom:2rem;
	position:relative;
	width:100%;
}

.block-chart.scatter .chart,
.block-chart .chart {
	min-height:20rem;
}

	@media screen and (min-width:750px) {
		.block-chart.parliament .chart {
			min-height:25rem;
		}
		
		.block-chart.scatter .chart {
			min-height:30rem;
		}
	}
	
.block-chart .chart .chart-loading {
	bottom:0;
	display:block;
	height:auto;
	left:0;
	opacity:0;
	position:absolute;
	right:0;
	top:0;
	width:auto;
	z-index:-1;
}

.block-chart .chart .chart-loading {
	opacity:1;
	z-index:2;
}

.block-chart .chart.ready .chart-loading {
	opacity:0;
	z-index:-1;
}

.block-chart .chart .chart-dataviz {
	display:block;
	height:0;
	opacity:0;
	overflow:hidden;
	width:100%;
}

.block-chart .chart.ready .chart-dataviz {
	height:auto;
	opacity:1;
	z-index:2;
}



