.article {
	display:block;
	list-style: none;
	margin:0 0 8rem 0;
	padding:0;
	position:relative;
}

	@media screen and (min-width:750px) {
		.article {
			-ms-flex-item-align:auto;
			    align-self:auto;
			-ms-flex:1 1 50%;
			    flex:1 1 50%;
			max-width:50%;
			-ms-flex-order:0;
			    order:0;
			padding:0 2rem;
		}
	}

	@media screen and (min-width:1360px) {
		.article {
			-ms-flex-item-align:auto;
			    align-self:auto;
			-ms-flex:1 1 33%;
			    flex:1 1 33%;
			margin-bottom:9rem;
			max-width:33%;
			-ms-flex-order:0;
			    order:0;
			padding:0 3rem;
		}
	}

	.article header h2 {
		display: block;
		font-size: .9rem;
		font-weight: 400;
		line-height:1;
		margin:0 0 1.5rem;
		text-transform: uppercase;
	}

	.article header h1 {
		color:#333;
		display: block;
		font-family: 'Playfair Display', Serif;
		font-size: 2rem;
		font-style: italic;
		font-weight: 700;
		line-height:1.5;
		margin:0 0 1.5rem 0;
		overflow-wrap: break-word;
		padding-right:4rem;
		text-transform: none;
		word-break: break-word;
	}
		
		@media screen and (min-width:750px) {
			.article header h1 {
				font-size:2.2rem;
			}
		}
		
		@media screen and (min-width:1200px) {
			.article header h1 {
				font-size:2.4rem;
			}
		}
	
		.article header h1 a {
			color:#333;
			text-decoration: none;
		}
		
			.article header h1 a:hover {
				text-decoration: underline;
			}
	
	.article p {
		display: block;
		font-family: 'Playfair Display', Serif;
		font-size: 1.6rem;
		font-weight: 400;
		line-height:1.5;
		margin:0 0 1.5rem 0;
		overflow-wrap: break-word;
		text-transform: none;
		word-break: break-word;
	}
		
		@media screen and (min-width:750px) {
			.article p {
				font-size:1.8rem;
			}
		}
	
		.article p:empty {
			display:none;
			margin:0;
		}
	
		.article p a {
			color:#333;
			text-decoration: none;
		}
		
			.article p a:hover {}
			
	.article .tags,
	.article .collections {
		list-style: none;
		margin:0 0 1.5rem 0;
		padding:0;
	}		
	
	.article .intro,
	.article .tag,
	.article .collection {
		display:inline-block;
		font-size:1rem;
		line-height:1.4rem;
		list-style: none;
		margin:0 .5rem 0 0;
		padding:0;
		position:relative;
	}
	
	@media screen and (min-width:1200px) {
		.article .intro,
		.article .tag,
		.article .collection {
			font-size:1.1rem;
		}
	}
	
	.article .intro {
		font-family: 'Playfair Display', Serif;
		font-size:1.2rem;
		font-style: italic;
		font-weight: 400;
	}
	
	@media screen and (min-width:1200px) {
		.article .intro {
			font-size:1.4rem;
		}
	}
	
	.article .tag:last-child:after,
	.article .tag:only-child:after {
		content:".";
	}
	
	.article .tag a,
	.article .collection a {
		color:#333;
		letter-spacing:.1rem;
		text-decoration: none;
		text-transform: uppercase;
		-webkit-transition: all .25s ease-in-out;
		-o-transition: all .25s ease-in-out;
		transition: all .25s ease-in-out;
	}
	
	.article .tag:after {
		content:",";
	}
	
	.article .tag a span {
		background-color:#ffff9e;
		-webkit-box-shadow:0 0 0 .2rem #ffff9e;
		        box-shadow:0 0 0 .2rem #ffff9e;
		display:inline-block;
		-webkit-transition: all .25s ease-in-out;
		-o-transition: all .25s ease-in-out;
		transition: all .25s ease-in-out;
	}
	
	.article .tag a span:before {
		content:"#";
	}
	
	.article .tag a:hover span,
	.article .tag a:active span {
		background-color: #ffff00;
		-webkit-box-shadow:0 0 0 .2rem #ffff00;
		        box-shadow:0 0 0 .2rem #ffff00;
	}
	
	.article .collection a:hover,
	.article .collection a:active {
		text-decoration: underline;
	}
	
	.article .collection a {
		padding-left:1.4rem;
		position:relative;
	}
	
	.article .collection a::before {
		background:#eee;
		border-radius:0;
		content: "";
		display:block;
		height:.8rem;
		left:.5rem;
		position:absolute;
		top:50%;
		-webkit-transform: rotate(45deg) translate3d(-50%,-50%,0);
		        transform: rotate(45deg) translate3d(-50%,-50%,0);
		-webkit-transform-origin: 0 0;
		    -ms-transform-origin: 0 0;
		        transform-origin: 0 0;
		width:.8rem;
	}
	
	.article .collection.centro-sinistra a::before {
		background:#F55F23;
	}
	
	.article .collection.sinistra a::before {
		background:#D0021B;
	}
	
	.article .collection.destra a::before {
		background:#000;
	}
	
	.article .collection.centro-destra a::before {
		background:#4A90E2;
	}
	
	.article .collection.m5s a::before {
		background:#F8E71C;  
	}
		
.article.popular {
	padding:6rem 0 ;
}

	@media screen and (min-width:750px) {	
		.article.popular {
			-ms-flex:0 0 100%;
			    flex:0 0 100%;
			max-width:100%;
			padding:8rem 2rem;
		}
	}

	@media screen and (min-width:1360px) {	
		.article.popular {
			-ms-flex:0 0 66%;
			    flex:0 0 66%;
			max-width:66%;
			padding:0 3rem;
		}
		
		.article.popular + .article.popular {
			-ms-flex:0 0 33%;
			    flex:0 0 33%;
			max-width:33%;
		}
	}

	.article.popular header h1 {
		font-family:'Zilla Slab', Cursive;
		font-size:2.2rem;
		font-style:normal;
		font-weight:700;
		letter-spacing:.2rem;
		line-height:1.4;
		padding-right:0;
		text-transform: uppercase;
	}
		
		.article.popular + .article.popular header h1 {
			font-size:2.2rem;
		}
	
	.article.popular header h1 a span {
		background-color:#c2f985; /*#ffff00;*/
		-webkit-box-shadow:0 0 0 .2rem #c2f985;
		        box-shadow:0 0 0 .2rem #c2f985; /*#ffff00;*/
	}
	
	.article.popular header h1 a:hover,
	.article.popular header h1 a:active {
		text-decoration:none;
	}
	
	.article.popular header h1 a:hover span,
	.article.popular header h1 a:active span {
		background:#fff;
		-webkit-box-shadow: 0 0 0 .2rem #fff;
		        box-shadow: 0 0 0 .2rem #fff;
	}
	
	@media screen and (min-width:750px) {
		.article.popular header h1 {
			font-size:3rem;
			max-width:80rem;
		}
		
		.article.popular + .article.popular header h1 {
			font-size:2.2rem;
		}
		
		.article.popular p {
			max-width:80rem;
		}
	}
	
	@media screen and (min-width:1360px) {
		.article.popular header h1 {
			font-size:3.6rem;
		}
		
		.article.popular + .article.popular header h1 {
			font-size:2.6rem;
		}
	}
	
	
.article.message {
	display:block;
	list-style: none;
	margin:0 0 8rem 0;
	padding:0;
	position:relative;
}

	@media screen and (min-width:750px) {
		.article.message {
			-ms-flex-item-align:auto;
			    align-self:auto;
			-ms-flex:1 1 100%;
			    flex:1 1 100%;
			max-width:100%;
			-ms-flex-order:0;
			    order:0;
			padding:0 2rem;
		}
	}

	@media screen and (min-width:1360px) {
		.article.message {
			-ms-flex-item-align:auto;
			    align-self:auto;
			-ms-flex:1 1 50%;
			    flex:1 1 50%;
			margin-bottom:9rem;
			max-width:50%;
			-ms-flex-order:0;
			    order:0;
			padding:0 3rem;
		}
	}
	
.article.message a {
	text-decoration: underline;
}


