.main-nav {
	background:#fff;
	bottom:0;
	display:none;
	height:auto;
	left:0;
	opacity:0;
	overflow:auto;
	position:fixed;
	right:0;
	top:4rem;
	-webkit-transition: opacity .25s ease-in-out;
	-o-transition: opacity .25s ease-in-out;
	transition: opacity .25s ease-in-out;
	z-index:-1;
	-webkit-overflow-scrolling: touch;
}

	.nav-visible .main-nav {
		display:block;
		opacity:0;
		z-index:9998;
	}

	.nav-visible.nav-in .main-nav {
		opacity:1;
	}

	@media screen and (min-width:1200px) {
		/*
		.home .main-nav {
			display:none;
		}

		.home.nav-visible .main-nav {
			display:block;
			opacity:0;
			z-index:9998;
		}

		.home.nav-visible.nav-in .main-nav {
			opacity:1;
		}
		*/
		.nav-visible.nav-in .main-nav,
		.nav-visible .main-nav,
		.main-nav {
			background: transparent;
			-webkit-box-shadow: none;
			        box-shadow: none;
			bottom:0;
			display:block;
			left:0;
			opacity:1;
			padding-top:20rem;
			right:auto;
			top:0;
			width:25rem;
			z-index:9998;
		}
	}
	
	@media screen and (min-width:1360px) {
		.nav-visible.nav-in .main-nav,
		.nav-visible .main-nav,
		.main-nav {
			width:24rem;
		}
	}

	.main-nav .nav-items {	
		height:auto;
		list-style:none;
		margin:0;
		padding:0 2rem;
		text-align:center;
		width:auto;
	}
	
		@media screen and (min-width:1200px) {
			.main-nav .nav-items {
				margin-left:4rem;
				padding:0;
				text-align:left;
			}
		}
	
		.main-nav .nav-items .nav-title {
			margin:4rem 0 1rem 0;
		}
		
			@media screen and (min-width:1200px) {
				.main-nav .nav-items .nav-title {
					margin-top:0;
				}
				
				.main-nav .nav-items .nav-item + .nav-title {
					margin-top:4rem;
				}
			}
	
			.main-nav .nav-items .nav-title h2 {
				font-family: 'Playfair Display', serif;
				font-size:2.2rem;
				font-style:italic;
				font-weight: 700;
				margin:0;
				padding:0;
				text-align:center;
			}
			
			@media screen and (min-width:1200px) {
				.main-nav .nav-items .nav-title h2 {
					font-size:1.8rem;
					text-align: left;
				}
			}
	
		.main-nav .nav-items .nav-item {
			display:block;
			font-family: 'Playfair Display', serif;
			font-size:2rem;
			font-style:italic;
			font-weight:400;
			height:auto;
			line-height:1.8;
			list-style:none;
			margin:0;
			padding:0;
			text-align:center;
		}
		
			@media screen and (min-width:1200px) {
				.main-nav .nav-items .nav-item {
					font-size:1.6rem;
					text-align: left;
				}
			}
		
		
			.main-nav .nav-items .nav-item a {
				color:#333;
				position:relative;
				text-decoration:none;
			}
			
			@media screen and (min-width:1200px) {
				.main-nav .nav-items .nav-item a {
					padding-left:1.5rem;
				}
				
				.main-nav .nav-items .nav-item a::before {
					background:#eee;
					border-radius:0;
					content: "";
					display:block;
					height:.8rem;
					left:.5rem;
					position:absolute;
					top:50%;
					-webkit-transform: rotate(45deg) translate3d(-50%,-50%,0);
					        transform: rotate(45deg) translate3d(-50%,-50%,0);
					-webkit-transform-origin: 0 0;
					    -ms-transform-origin: 0 0;
					        transform-origin: 0 0;
					width:.8rem;
				}
				
				.main-nav .nav-items .nav-item.centro-destra a::before {
					background:#4A90E2;
				}
				.main-nav .nav-items .nav-item.centro-sinistra a::before {
					background:#F55F23;
				}
				.main-nav .nav-items .nav-item.destra a::before {
					background:#000;
				}
				.main-nav .nav-items .nav-item.sinistra a::before {
					background:#D0021B;
				}
				.main-nav .nav-items .nav-item.m5s a::before {
					background:#F8E71C; 
				}
			}
			
			.main-nav .nav-items .nav-item a span {}
			
			.main-nav .nav-items .nav-item.active {}
			
				.main-nav .nav-items .nav-item.active a {
					text-decoration:underline;
				}
				
	.main-nav .nav-item.search #search-keyword {
		-webkit-appearance:none;
		background:rgba(200,200,200,.1);
		border:.1rem solid transparent;
		border-bottom:.1rem solid #333;
		display:block;
		font-family: 'Playfair Display', serif;
		font-size:1.6rem;
		font-style:italic;
		font-weight:400;
		height:4rem;
		line-height:4rem;
		margin:0 auto;
		max-width:50rem;
		outline:none;
		padding:.5rem;
		text-align:center;
		-webkit-transition:all .25s ease-in-out;
		-o-transition:all .25s ease-in-out;
		transition:all .25s ease-in-out;
		width:90%;
	}
	
	@media screen and (min-width:1200px) {
		.main-nav .nav-item.search #search-keyword {
			height:3.2rem;
			line-height:3.2rem;
			margin:0;
			width:100%;
		}
	}
	
	.main-nav .nav-item.search #search-keyword:active,
	.main-nav .nav-item.search #search-keyword:focus {
		background:#fff;
		border-color:#333;
		outline:none;
	}


