.slope {
  bottom: 0;
  height: auto;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  width: auto;
}

.slope svg {
  display: block;
  height: 100%;
  width: 100%;
}

.slope svg line {
  stroke: #999;
  stroke-width: .1rem;
}

.slope svg circle {
  fill: #999;
  stroke: transparent;
  stroke-width: 0;
}

.slope svg .axis text {
  alignment-baseline: middle;
  fill: #999;
  font-size: 1rem;
  text-anchor: start;
  text-transform: uppercase;
}

.slope svg .axis .axis--labels text {
  fill: #333;
  font-weight: 700;
  text-anchor: middle;
}

.slope svg .axis .axis--start text {
  text-anchor: end;
}

.slope svg .axis .axis--background path {
  fill: #ededed;
}

.slope svg .series .serie text {
  alignment-baseline: middle;
  fill: #333;
  font-size: 1rem;
  font-weight: 700;
  text-anchor: start;
  text-transform: uppercase;
}

.slope svg .series .serie .legend text {
  alignment-baseline: middle;
  font-size: .8rem;
}

.slope svg .series .serie .legend text tspan {
  alignment-baseline: middle;
}

.slope svg .series .serie .legend text tspan.data {
  font-size: 1rem;
}

.slope svg .series .serie-results .legend line {
  display: block;
  stroke: #e2e2e2 !important;
  stroke-dasharray: 0;
  stroke-width: 1rem;
  -webkit-transform: translate(0, -.1rem);
      -ms-transform: translate(0, -.1rem);
          transform: translate(0, -.1rem);
}

.slope svg .series .serie text.serie--label-start {
  text-anchor: end;
}

.slope svg .series .serie circle {
  fill: #333;
}

.slope svg .series .serie.serie-results circle {
  display: none;
}

.slope svg .series .serie line {
  stroke: #333;
}

.slope svg .series .serie-activity line {
  stroke-dasharray: 0;
  stroke-width: .2rem;
}

.slope svg .series .serie-global line {
  stroke-dasharray: 2 2;
  stroke-width: .2rem;
}

.slope svg .series .serie-global text.serie--label-start {
  display: none;
}

.slope svg .series .serie-results line {
  display: none;
  stroke-dasharray: 6 4;
  stroke-width: .1rem;
}

.slope svg .series .serie text.serie--label-middle {
  alignment-baseline: middle;
  text-anchor: middle;
}

.slope svg .series text.serie--label-middle,
.slope svg .series .serie-results text.serie--label-start,
.slope svg .series .serie-results text.serie--label-end {
  display: none;
}

.slope svg .series .serie-results text.serie--label-middle {
  display: block;
  -webkit-transform: translate(0, 1.5rem);
      -ms-transform: translate(0, 1.5rem);
          transform: translate(0, 1.5rem);
}

.slope svg .series .serie .serie--fill {
  fill:none;
}

.slope svg .series .serie.serie-results .serie--fill {
  fill:#e2e2e2;
  mix-blend-mode: multiply;
}

.destra .slope svg .series .serie line {
  stroke: #000;
}

.destra .slope svg .series .serie-global text.serie--label-end,
.destra .slope svg .series .serie circle {
  fill: #000;
}

.sinistra .slope svg .series .serie line {
  stroke: #D0021B;
}

.sinistra .slope svg .series .serie-global text.serie--label-end,
.sinistra .slope svg .series .serie circle {
  fill: #D0021B;
}

.centro-sinistra .slope svg .series .serie line {
  stroke: #F55F23;
}

.centro-sinistra .slope svg .series .serie-global text.serie--label-end,
.centro-sinistra .slope svg .series .serie circle {
  fill: #F55F23;
}

.m5s .slope svg .series .serie line {
  stroke: #F8E71C;
}

.m5s .slope svg .series .serie circle {
  fill: #F8E71C;
}

.centro-destra .slope svg .series .serie line {
  stroke: #4A90E2;
}

.centro-destra .slope svg .series .serie-global text.serie--label-end,
.centro-destra .slope svg .series .serie circle {
  fill: #4A90E2;
}


