.stats-tabs-content,
.stats-tabs {
	background:#fefefe;
	height:3rem;
	left:0;
	position:absolute;
	right:0;
	top:0;
	width:auto;
}

.stats-tabs-content {
	bottom:0;
	height:auto;
	top:3rem;
}

.stats-tabs ul {
	height:3rem;
	list-style: none;
	margin:0 0 0 -1rem;
	padding:0;
}

.stats-tabs .stats-tab {
	color:#999;
	display:inline-block;
	font-size:1rem;
	height:3rem;
	letter-spacing:.1rem;
	line-height:3rem;
	list-style: none;
	margin:0;
	padding:0 .5rem;
	text-transform: uppercase;
}

.stats-tabs .stats-tab a {
	color:#666;
	display:block;
	height:3rem;
	line-height:2.9rem;
	padding:0 .5rem;
	position:relative;
	text-decoration:none;
}

.stats-tabs .stats-tab a::after {
	bottom:0;
	background:rgba(20,20,20,0);
	content:"";
	display:block;
	height:.1rem;
	left:50%;
	position:absolute;
	-webkit-transform: translate3d(-50%,0,0);
	        transform: translate3d(-50%,0,0);
	-webkit-transition: all .25s ease-in-out;
	-o-transition: all .25s ease-in-out;
	transition: all .25s ease-in-out;
	width:0;
}

.stats-tabs .stats-tab.active a {
	color:#333;
}

.stats-tabs .stats-tab.active a::after {
	background:rgba(33,33,33,1);
	width: calc( 100% - 1rem );
}


