.chart-milestone {
	display:block;
	height:auto;
	width:auto;
}
	
	.chart-milestone svg.chart-milestone-viz {
		display:block;
		height:15rem;
		width:100%;
	}
	
		@media screen and (min-width:750px) {
			.chart-milestone svg.chart-milestone-viz {
				height:15rem;
			}
		}
	
	.chart-milestone .chart-milestone-viz.centro-sinistra .chart-milestone-viz-legend-popularity,
	.chart-milestone .chart-milestone-viz.centro-sinistra .chart-milestone-viz-popularity {
		fill:#F55F23;
	}
	
	.chart-milestone .chart-milestone-viz.sinistra .chart-milestone-viz-legend-popularity,
	.chart-milestone .chart-milestone-viz.sinistra .chart-milestone-viz-popularity {
		fill:#D0021B;
	}
	
	.chart-milestone .chart-milestone-viz.destra .chart-milestone-viz-legend-popularity,
	.chart-milestone .chart-milestone-viz.destra .chart-milestone-viz-popularity {
		fill:#ccc;
	}
	
	.chart-milestone .chart-milestone-viz.centro-destra .chart-milestone-viz-legend-popularity,
	.chart-milestone .chart-milestone-viz.centro-destra .chart-milestone-viz-popularity {
		fill:#4A90E2;
	}
	
	.chart-milestone .chart-milestone-viz.m5s .chart-milestone-viz-legend-popularity,
	.chart-milestone .chart-milestone-viz.m5s .chart-milestone-viz-popularity {
		fill:#F8E71C;  
	}
	
	.chart-milestone .chart-milestone-viz .chart-milestone-viz-legend-popularity,
	.chart-milestone .chart-milestone-viz .chart-milestone-viz-popularity {
		fill: #efefef; /* #e3e5dc; */
		mix-blend-mode:multiply;
	}
	
	.chart-milestone .chart-milestone-viz .chart-milestone-viz-legend-activity,
	.chart-milestone .chart-milestone-viz .chart-milestone-viz-activity {
		fill: #efefef;
		mix-blend-mode:multiply;
	}
	
	.chart-milestone .chart-milestone-viz .chart-milestone-viz-milestone {
		mix-blend-mod:multiply;
		stroke:#333;
		stroke-width:.1rem;
	}
	
	.chart-milestone .chart-milestone-viz .chart-milestone-viz-floor {
		mix-blend-mode:multiply;
		stroke:#efefef;
		stroke-width:.1rem;
	}
	
	.chart-milestone .chart-milestone-viz .chart-milestone-viz-arrow {
		fill:#333;
		mix-blend-mod:multiply;
		stroke:#fefefe;
		stroke-width:.1rem;
	}
	
	.chart-milestone .chart-milestone-viz .chart-milestone-viz-line-date {
		stroke:#333;
		stroke-dasharray: 2 5;
		stroke-width:.1rem;
	}
	
		.chart-milestone .chart-milestone-viz .chart-milestone-viz-line-date.chart-milestone-viz-line-date-solid {
			stroke:#333;
			stroke-dasharray: 1 0;
			stroke-width:.1rem;
		}
	
	.chart-milestone .chart-milestone-viz .chart-milestone-viz-label {
		font-size:.9rem;
		font-weight:400;
		letter-spacing:.1rem;
		text-transform: uppercase;
	}
	
		.chart-milestone .chart-milestone-viz .chart-milestone-viz-label tspan {
			font-family: 'Zilla Slab', Cursive;
			font-size:1.1rem;
			font-weight:700;
		}
	
	.chart-milestone .chart-milestone-viz .chart-milestone-viz-label.chart-milestone-viz-milestone-label {
		font-family: 'Zilla Slab', Cursive;
		font-size:1.2rem;
		font-weight:700;
	}
	
	.chart-milestone .chart-milestone-container {
		background:#fefefe;
	}
	
	.chart-milestone .chart-milestone-tags,	
	.chart-milestone .chart-milestone-text {
		margin:0;
		padding:2rem 4rem;
	}
	
	.chart-milestone .chart-milestone-detail h1 {
		color:#333;
		font-family: 'Zilla Slab', Cursive;
		font-size:1.4rem;
		font-weight:700;
		line-height:1.5;
		margin:0;
		padding:0 2rem;
		text-align:center;
		text-transform: uppercase;
	}
	
		@media screen and (min-width:750px) {
			.chart-milestone .chart-milestone-detail h1 {
				font-size:1.6rem;
				padding:0 6rem;
			}
		}

		@media screen and (min-width:1360px) {
		.chart-milestone .chart-milestone-detail h1 {
			font-size:1.8rem;
		}
	}
	
	.chart-milestone .chart-milestone-text p {
		font-family: 'Playfair Display', Serif;
		margin:0;
	}
	
		.chart-milestone .chart-milestone-text p a {
			color:#333;
			font-style:italic;
		}


