.intro {
	height: auto;
	margin:0 0 4rem 0;
	position: relative;
}
	@media screen and (min-width:750px) {
		.intro {
			margin-bottom:6rem;
			padding-top: 0;
		}
	}
	@media screen and (min-width:1200px) {
		.intro {
			margin-bottom:8rem;
			padding-top: 0;
		}
	}

	.intro--claim--text {
		margin: 8rem 0 12rem 0;
		max-width: 60rem;
	}

	@media screen and (min-width:750px) {

		.intro--claim {
			min-height: 60vh;
		}

		.intro--claim--text {
			margin:0;
			position: absolute;
			top: 50%;
			-webkit-transform: translate3d(0, -50%, 0);
			        transform: translate3d(0, -50%, 0);
		}
	}

	@media screen and (min-width:1200px) {
		.intro--claim {
			min-height: 60vh;
		}
	}

	.intro--claim--text h1 {
		font-family: 'Playfair Display', Serif;
		font-size:3rem;
		font-style:italic;
		font-weight:700;
		line-height:1.6em;
	}

	@media screen and (min-width:750px) {
		.intro--claim--text h1 {
			font-size:3.8rem;
		}
	}

	@media screen and (min-width:1200px) {
		.intro--claim--text h1 {
			font-size:4.8rem;
		}
	}






/**
 *
 * COLLECTIONS
 *
 **/

.item--text--title h1, 
.item--text--title h2 {
  display: block;
  font-family: 'Playfair Display', Serif;
  font-size: 1.8rem;
  font-style: italic;
  font-weight: 700;
  line-height: 1.5;
  margin: 0 0 2rem;
  overflow-wrap: break-word;
  padding-right: 4rem;
  text-transform: none;
  word-break: break-word;
}

.item--text--title h1 {
  font-size:2.4rem;
}

.item--text--body a {
  color:#333;
}

.item--text--body h3 {
  font-size: 1.4rem;
	font-weight: 400;
	margin-top:0;
  text-transform: uppercase;
}

.item--text--body p {
  margin: 0 0 1.5em 0;
}

@media screen and (min-width: 750px) {
  .item--text--title h2 {
    font-size: 2rem;
  }

  .item--text--title h1 {
    font-size:2.6rem;
  }

  .item--text--body {
    -webkit-columns: auto 3;
       -moz-columns: auto 3;
            columns: auto 3;
  }
}




/**
 *
 * ARTICLES
 *
 **/

 .item--milestones--title h2 {
  display: block;
  font-size: 1.4rem;
  font-weight: 300;
  letter-spacing: .2em;
  line-height: 1.5;
  margin: 2rem 0 4rem;
  overflow-wrap: break-word;
  text-align: center;
  text-transform: uppercase;
  word-break: break-word;
 }

 .item--milestones .item--milestone {
  margin-bottom: 4rem;
  position: relative;
 }

.item--milestones .item--milestone--title h1,
.item--milestones .item--milestone--title h2,
.item--milestones .item--milestone--title h3 {
  margin: 0;
}

.item--milestones .item--milestone--title h2 {
  border: .1rem solid #333;
  border-radius: 50%;
  color: #333;
  display: block;
  font-size: 1.5rem;
  height: 3rem;
  line-height: 3rem;
  margin: 0 auto 1.5rem auto;
  text-align: center;
  width: 3rem;
}
.item--milestones .item--milestone--title h3 {
  display: block;
  font-size: .9rem;
  font-weight: 400;
  letter-spacing: .15em;
  line-height: 1;
  margin: 0 0 1rem;
  text-align: center;
  text-transform: uppercase;
}
.item--milestones .item--milestone--title h1 {
  color: #333;
  display: block;
  font-family:'Playfair Display',Serif;
  font-size: 1.6rem;
  font-style: italic;
  font-weight: 700;
  line-height: 1.5;
  margin: 0 0 1.5rem;
  overflow-wrap: break-word;
  text-align: center;
  text-transform: none;
  word-break: break-word;
}

.item--milestones .item--milestone--title h1 a {
  color: #333;
  text-decoration: none;
}

.item--milestones .item--milestone--title h1 a:hover {
  color: #333;
  text-decoration: underline;
}

.item--milestone--body {
  display: none;
}

.item--milestone--body p {
  display: block;
  font-family: 'Playfair Display',Serif;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 1.5;
  margin: 0 0 1.5rem;
  overflow-wrap: break-word;
  text-transform: none;
  word-break: break-word;
}

.item--milestone--body a {
  color: #333;
  text-decoration: none;
}

.item--milestone--body a:hover {
  color: #333;
  text-decoration: underline;
}

.tags ol,
.item--milestone--tags ol {
  list-style: none;
  margin: 0 0 1.5rem;
  padding: 0;
  text-align: center;
}

.tags li,
.item--milestone--tags li {
  display: inline-block;
  font-size: 1rem;
  line-height: 1.4rem;
  list-style: none;
  margin: 0 .5rem;
  padding: 0;
  position: relative;
}

.tags li.intro,
.item--milestone--tags li.intro {
  display: none;
  font-family: 'Playfair Display',Serif;
  font-size: 1.2rem;
  font-style: italic;
  font-weight: 400;
}

.tags li a,
.item--milestone--tags li a {
  color: #333;
  letter-spacing: .1rem;
  text-decoration: none;
  text-transform: uppercase;
  -webkit-transition: all .25s ease-in-out;
  -o-transition: all .25s ease-in-out;
  transition: all .25s ease-in-out;
}

.tags li a span,
.item--milestone--tags li a span {
  background-color: #ffff9e;
  -webkit-box-shadow: 0 0 0 .1rem #ffff9e;
          box-shadow: 0 0 0 .1rem #ffff9e;
  display: inline-block;
  -webkit-transition: all .25s ease-in-out;
  -o-transition: all .25s ease-in-out;
  transition: all .25s ease-in-out;
}

.tags li a span:before,
.item--milestone--tags li a span:before {
  content:"#";
}

.tags li a:active span,
.tags li a:hover span,
.item--milestone--tags li a:active span,
.item--milestone--tags li a:hover span {
  background-color: #ffff00;
  -webkit-box-shadow:0 0 0 .2rem #ffff00;
          box-shadow:0 0 0 .2rem #ffff00;
}

@media screen and (min-width: 750px) {
  .item--milestones {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }

  .item--milestone {
    -ms-flex: 0 0 33.3333%;
        flex: 0 0 33.3333%;
  }

  .item--milestones .item--milestone--title h1 {
    font-size: 1.8rem;
    padding-right: 4rem;
    text-align: left;
  }

  .item--milestones .item--milestone--title h2 {
    margin-left: 0;
    margin-right: 0;
  }

  .item--milestones .item--milestone--title h3 {
    text-align: left;
  }

  .item--milestone--body {
    display: block;
    padding-right: 4rem;
  }

  .item--milestone--tags ol {
    padding-right: 4rem;
    text-align: left;
  }

  .tags li.intro,
  .item--milestone--tags li.intro {
    display: inline-block;
  }

  .tags li:after,
  .item--milestone--tags li:after {
    content: ", ";
  }

  .tags li:last-child:after,
  .item--milestone--tags li:last-child:after {
    content: ".";
  }
}

@media screen and (min-width: 1020px) {}

@media screen and (min-width: 1200px) {
  .item--milestone--body p {
    font-size:1.6rem;
  }
}

/**
 *
 * STATS
 *
 **/

.item--stats.data {
  display:block;
  margin: 4rem 0;
}

.data--item {
  margin-bottom: 4rem;
  text-align: center;
}

.data--item--title h1,
.data--item--title h2,
.data--item--title h3,
.data--item--title h4 {
  margin: 0;
}

.data--item--title h1:empty,
.data--item--title h2:empty,
.data--item--title h3:empty,
.data--item--title h4:empty {
  display: none;
}

.data--item--title h2 {
  display: block;
  font-size: 1.2rem;
  font-weight: 300;
  height: 2.6rem;
  line-height: 1.2;
  margin: 0;
  overflow-wrap: break-word;
  text-transform: uppercase;
  word-break: break-word;
}

.data--item--body h1 {
  color: #333;
  display: block;
  font-family: 'Playfair Display', Serif;
  font-size: 4.4rem;
  font-style: italic;
  font-weight: 400;
  line-height: .9;
  margin: 0;
  overflow-wrap: break-word;
  text-transform: none;
  word-break: break-word;
}

.data--item--body h2 {
  color: #333;
  display: block;
  font-family: 'Playfair Display', Serif;
  font-size: 2.2rem;
  font-style: italic;
  font-weight: 400;
  line-height: .9;
  margin: 1.4rem 0 0 0;
  overflow-wrap: break-word;
  text-transform: none;
  word-break: break-word;
}

.data--item--body h3 {
  color: rgb(155, 155, 155);
  display: block;
  font-size: 1rem;
  font-weight: 400;
  line-height: .9;
  margin: 1.4rem 0 0 0;
  overflow-wrap: break-word;
  text-transform: uppercase;
  word-break: break-word;
}

.data--item--body h4 {
  color: rgb(155, 155, 155);
  display: block;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.1;
  margin: 1.4rem 0 0 0;
  overflow-wrap: break-word;
  padding:0 2rem;
  text-transform: none;
  word-break: break-word;
}

@media screen and (min-width: 750px) {
  .item--stats.data {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 8rem 0;
    position: relative;
  }
  
  .data--item:after  {
    background:#333;
    bottom: 0;
    content:"";
    display: block;
    height: .1rem;
    left: 35%;
    position:absolute;
    right: 35%;
    width: auto;
  }

  .data--item {
    -ms-flex: 0 0 20%;
        flex: 0 0 20%;
    margin-bottom: 0;
    padding-bottom: 3rem;
    position: relative;
  }
}

/*
 * ARCHIVES
 */

 .archives {
	max-width: 120rem;
 }


