.summary {
  margin-bottom:4rem;
  min-height: 100vh;
  padding: 0;
}

@media screen and (min-width:750px) {
  .summary {
    margin-bottom:6rem;
  }
}

@media screen and (min-width:1200px) {
  .summary {
    margin-bottom:8rem;
  }
}

.summary--chart {
  background-color: #fefefe;
  display: block;
  height: 40vh;
  left: 0;
  margin: 0 -4rem;
  max-width: 120rem;
  position: -webkit-sticky;
  position: sticky;
  right: 0;
  top: 0;
  width: calc(100% + 8rem);
  z-index: 2;
}

.summary--chart--switch {
  display: inline-block;
  height: 4rem;
  left: 1rem;
  mix-blend-mode: multiply;
  opacity: 1;
  padding:.2rem 0;
  position: absolute;
  text-align: center;
  top: 50%;
  -webkit-transform: translate3d(0,-50%,0);
          transform: translate3d(0,-50%,0);
  -webkit-transition: opacity .25s ease-in-out;
  -o-transition: opacity .25s ease-in-out;
  transition: opacity .25s ease-in-out;
  width: 2.5rem;
  z-index: 9;
}

.summary--chart--switch:active,
.summary--chart--switch:hover {
  mix-blend-mode: normal;
}

[data-step="-1"] .summary--chart--switch,
[data-step="0"] .summary--chart--switch {
  opacity: 0;
}

.summary--chart--switch button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: #fff;
  border: .1rem solid #333;
  color: #333;
  display: block;
  font-size: 1.2rem;
  font-weight: 700;
  height: 2rem;
  line-height: 1.8rem;
  outline: none;
  padding:0;
  text-align:center;
  width: 2.5rem;
}

.summary--chart--switch button.active {
  background: #333;
  color: #fff;
}

.summary--chart--switch button:first-child {
  border-radius: .3rem .3rem 0 0; 
}

.summary--chart--switch button:last-child {
  border-top: 0;
  border-radius: 0 0 .3rem .3rem; 
}

.summary--chart--wrapper {
  bottom: 0;
  -webkit-box-shadow:0 .4rem 8rem -2rem rgba(0,0,0,.2);
          box-shadow:0 .4rem 8rem -2rem rgba(0,0,0,.2);
  height: auto;
  left: 0;
  position: absolute;
  right: 0; 
  top: 4rem;
  -webkit-transition: -webkit-box-shadow .75s ease-in-out;
  transition: -webkit-box-shadow .75s ease-in-out;
  -o-transition: box-shadow .75s ease-in-out;
  transition: box-shadow .75s ease-in-out;
  transition: box-shadow .75s ease-in-out, -webkit-box-shadow .75s ease-in-out;
  width: auto;
}

[data-step="-1"] .summary--chart--wrapper {
  -webkit-box-shadow: none;
          box-shadow: none;
}

.summary--legend {
  display: block;
  height: auto;
  margin: 0;
  max-width: 120rem;
  min-height: 40vh;
  padding: 2rem 0;
  width: 100%;
  z-index: 1;
}

.summary--legend--item {
  margin-bottom: 6rem;
  min-height: 40vh;
  position: relative;
}

@media screen and (min-width:750px) {
  .summary--chart {
    height: 40vh;
  }
  .summary--chart--switch {
    bottom: 1rem;
  }

  .summary--chart--wrapper {
    bottom: 0;
    top: 6rem;
  }
}

@media screen and (min-width:1020px) {
  .summary--chart {
    height: 40vh;
    top: 0;
  }

  .summary--chart--wrapper {
    width: auto;
  }

  .summary--legend--item {
    margin-bottom: 8rem;
  }

  .summary--legend--item:only-child {
    margin-bottom:0;
  }
}

@media screen and (min-width:1200px) {
  .summary--chart {
    height: 50vh;
    margin: 0;
    padding-top: 8rem;
    top: 0;
    width: 100%;
  }

  .summary--legend--item {
    margin-bottom: 10rem;
  }
}




/**
 *
 * CHART
 *
 **/

.summary--chart--wrapper svg {
  height: 100%;
  width: 100%;
}

/*
 * Areas
 */
.summary--chart--wrapper svg .sinistra,
.summary--chart--wrapper svg .centro-sinistra,
.summary--chart--wrapper svg .m5s,
.summary--chart--wrapper svg .centro-destra,
.summary--chart--wrapper svg .destra {
  fill: #efefef;
  stroke: rgba(239, 239, 239,.5);
  stroke-dasharray: 0;
  stroke-width: .1rem;
  -webkit-transition: fill .75s ease-in-out;
  -o-transition: fill .75s ease-in-out;
  transition: fill .75s ease-in-out;
  will-change: fill;
}

/* 0 */
[data-step="0"] .summary--chart--wrapper svg .sinistra,
[data-step="0"] .summary--chart--wrapper svg .centro-sinistra,
[data-step="0"] .summary--chart--wrapper svg .m5s,
[data-step="0"] .summary--chart--wrapper svg .centro-destra,
[data-step="0"] .summary--chart--wrapper svg .destra {
  fill: #efefef;
}

/* 1 */
[data-step="1"] .summary--chart--wrapper svg .sinistra,
[data-step="1"] .summary--chart--wrapper svg .centro-sinistra,
[data-step="1"] .summary--chart--wrapper svg .m5s,
[data-step="1"] .summary--chart--wrapper svg .centro-destra {
  fill: #efefef;
}
[data-step="1"] .summary--chart--wrapper svg .destra {
  fill: #000;
}

/* 2 */
[data-step="2"] .summary--chart--wrapper svg .centro-destra,
[data-step="2"] .summary--chart--wrapper svg .centro-sinistra,
[data-step="2"] .summary--chart--wrapper svg .m5s {
  fill: #efefef;
}
[data-step="2"] .summary--chart--wrapper svg .sinistra {
  fill: #d0021b;
}
[data-step="2"] .summary--chart--wrapper svg .destra {
  fill: #000;
}

/* 3 */
[data-step="3"] .summary--chart--wrapper svg .m5s,
[data-step="3"] .summary--chart--wrapper svg .centro-destra {
  fill: #efefef;
}
[data-step="3"] .summary--chart--wrapper svg .centro-sinistra {
  fill: #f55f23;
}
[data-step="3"] .summary--chart--wrapper svg .sinistra {
  fill: #d0021b;
}
[data-step="3"] .summary--chart--wrapper svg .destra {
  fill: #000;
}

/* 4 */
[data-step="4"] .summary--chart--wrapper svg .centro-destra {
  fill: #efefef;
}
[data-step="4"] .summary--chart--wrapper svg .centro-sinistra {
  fill: #f55f23;
}
[data-step="4"] .summary--chart--wrapper svg .m5s {
  fill: #f8e71c;
}
[data-step="4"] .summary--chart--wrapper svg .sinistra {
  fill: #d0021b;
}
[data-step="4"] .summary--chart--wrapper svg .destra {
  fill: #000;
}

/* 5, 6 */
[data-step="6"] .summary--chart--wrapper svg .sinistra,
[data-step="5"] .summary--chart--wrapper svg .sinistra {
  fill: #d0021b;
}
[data-step="6"] .summary--chart--wrapper svg .centro-sinistra,
[data-step="5"] .summary--chart--wrapper svg .centro-sinistra {
  fill: #f55f23;
}
[data-step="6"] .summary--chart--wrapper svg .m5s,
[data-step="5"] .summary--chart--wrapper svg .m5s {
  fill: #f8e71c;
}
[data-step="6"] .summary--chart--wrapper svg .centro-destra,
[data-step="5"] .summary--chart--wrapper svg .centro-destra {
  fill: #4a90e2;
}
[data-step="6"] .summary--chart--wrapper svg .destra,
[data-step="5"] .summary--chart--wrapper svg .destra {
  fill: #000;
}

/*
 * Lines
 */
.summary--chart--wrapper svg .x-axis line {
  stroke: #333;
  stroke-width: .1rem;
}

.summary--chart--wrapper svg .vertical-lines line {
  stroke: #333;
  stroke-dasharray: 0;
  stroke-width: .1rem;
}

.summary--chart--wrapper svg .vertical-lines path.electionArea {
  fill: rgba(255,255,255,.5);
  stroke: transparent;
  stroke-width: 0;
}

.summary--chart--wrapper svg .vertical-lines line.electionLine {
  stroke: #333;
  stroke-dasharray: 2 2;
  stroke-width: .1rem;
}

.summary--chart--wrapper svg .vertical-lines-labels text {
  fill: #333;
  font-family: 'Nunito Sans', sans-serif; 
  font-size: 1.1rem;
  font-weight: 300;
  text-transform: uppercase;
}

.summary--chart--wrapper svg .x-axis g text.label-date {
  visibility:hidden;
  font-size: 1.1rem;
  font-weight: 300;
}

.summary--chart--wrapper svg .x-axis g:nth-child(2n) text.label-date {
  visibility: visible;
}

@media screen and (min-width:750px) {
  .summary--chart--wrapper svg .x-axis g text.label-date {
    visibility: visible;
  }
}

.summary--chart--wrapper svg .x-axis text.label-date,
.summary--chart--wrapper svg .vertical-lines-labels text.label-election {
  text-anchor: middle;
}

.summary--chart--wrapper svg .vertical-lines-labels text.label-end {
  text-anchor: end;
}

.summary--chart--wrapper svg .horizontal-lines {
  opacity: 1;
  -webkit-transition: opacity .25s ease-in-out;
  -o-transition: opacity .25s ease-in-out;
  transition: opacity .25s ease-in-out;
}

[data-step="-1"] .summary--chart--wrapper svg .horizontal-lines {
  opacity: 0;
}

.summary--chart--wrapper svg .horizontal-lines .arrow {
  fill: #333;
}

.summary--chart--wrapper svg .horizontal-lines line.stopper {
  stroke: #333;
  stroke-dasharray: 0;
  stroke-width: .1rem;
}

.summary--chart--wrapper svg .horizontal-lines line.line {
  stroke: #333;
  stroke-dasharray: 4 2;
  stroke-width: .1rem;
}

.summary--chart--wrapper svg .horizontal-lines text.label {
  alignment-baseline: middle;
  fill: #333;
  font-family: 'Nunito Sans', sans-serif; 
  font-size: 1rem;
  font-weight: 300;
  text-anchor: middle;
  text-transform: uppercase;
}

/*
 * Milestones
 */

.summary--chart--wrapper svg .milestones-lines line {
  stroke: #333;
  stroke-dasharray: 2 5;
  stroke-width: .1rem;
}

.summary--chart--wrapper svg .milestones-lines path {
  fill: #333;
  stroke: transparent;
  stroke-width: 0;
}

.summary--chart--wrapper svg .milestones-lines circle {
  fill: rgba(255,255,255,.95);
  stroke: #333;
  stroke-width: .1rem;
}

.summary--chart--wrapper svg .milestones-lines text {
  alignment-baseline: middle;
  fill: #333;
  font-family: 'Nunito Sans', sans-serif; 
  font-size: 1.2rem;
  font-weight: 300;
  text-anchor: middle;
}

.summary--chart--wrapper svg .milestones-lines-group {
  opacity: 0;
  -webkit-transition: opacity .5s ease-in-out;
  -o-transition: opacity .5s ease-in-out;
  transition: opacity .5s ease-in-out;
}

[data-step="0"] .summary--chart--wrapper svg .milestones-lines-group.milestones-lines-intro {
  opacity: 1;
}

[data-step="1"] .summary--chart--wrapper svg .milestones-lines-group.milestones-lines-destra {
  opacity: 1;
}

[data-step="2"] .summary--chart--wrapper svg .milestones-lines-group.milestones-lines-sinistra {
  opacity: 1;
}

[data-step="3"] .summary--chart--wrapper svg .milestones-lines-group.milestones-lines-centro-sinistra {
  opacity: 1;
}

[data-step="4"] .summary--chart--wrapper svg .milestones-lines-group.milestones-lines-m5s {
  opacity: 1;
}

[data-step="5"] .summary--chart--wrapper svg .milestones-lines-group.milestones-lines-centro-destra {
  opacity: 1;
}



