	.article .stats-wrapper {
		background:#eee;
		display:block;
		height:10.7rem;
		position:relative;
		width:100%;
	}
	
	.article .stats-wrapper .stats-front,
	.article .stats-wrapper .stats-back {
		-webkit-backface-visibility: hidden;
		        backface-visibility: hidden;
		background:#fefefe;
		bottom:0;
		display:block;
		left:0;
		opacity:0;
		position:absolute;
		right:0;
		top:0;
		-webkit-transform: rotateX(0deg);
		        transform: rotateX(0deg);
		-webkit-transform-origin: center center;
		    -ms-transform-origin: center center;
		        transform-origin: center center;
		-webkit-transform-style: preserve-3d;
		        transform-style: preserve-3d;
		-webkit-transition:all .5s ease-in-out;
		-o-transition:all .5s ease-in-out;
		transition:all .5s ease-in-out;
		z-index:1;
		
		/*background:rgba(0,0,0,.1);*/
	} 
	
	.article .stats-wrapper .stats-back {
		-webkit-transform: rotateX(-180deg);
		        transform: rotateX(-180deg);
	}
	
	.article .stats-wrapper.show-front .stats-front, 
	.article .stats-wrapper.show-back .stats-back {
		opacity:1;
		-webkit-transform: rotateX(0deg);
		        transform: rotateX(0deg);
	}
	
	.article .stats-wrapper.show-back .stats-front {
		-webkit-transform: rotateX(180deg);
		        transform: rotateX(180deg);
	}
	.article .stats-wrapper.show-front .stats-back {
		-webkit-transform: rotateX(-180deg);
		        transform: rotateX(-180deg);
	}


