.keywords {
  min-height: 100vh;
}

.keywords--legend {
  margin-bottom: 4rem;
  max-width: 120rem;
}

@media screen and (min-width:750px) {
  .keywords--legend {
    margin-bottom: 6rem;
  }
}

@media screen and (min-width:1200px) {
  .keywords--legend {
    margin-bottom:8rem;
  }
}

.keywords .keywords--data { 
  max-width: 120rem;
}

.keywords .item { 
  margin-bottom: 4rem;
}

@media screen and (min-width:750px) {
  .keywords .item {
    margin-bottom: 6rem;
  }

  .keywords .keywords--data .item {
    min-height: 50vh;
  }
}

@media screen and (min-width:1020px) {
  .keywords .item {
    margin-bottom: 8rem;
  }

  .keywords .keywords--data .item {
    min-height: 40vh;
  }
}

.keywords .tags ol {
  text-align: left;
}

.keywords--chart {
  background:#fefefe;
  display: block;
  height: 40vh;
  left:0;
  margin: 0 -4rem 4rem -4rem;
  max-width: 120rem;
  padding:0;
  position: -webkit-sticky;
  position: sticky;
  right: 0;
  top: 0;
  width: auto;
  z-index:2;
}

.keywords--chart .keywords--chart--wrapper {
  background:#fefefe;
  -webkit-box-shadow:0 .4rem 8rem -2rem rgba(0,0,0,.2);
          box-shadow:0 .4rem 8rem -2rem rgba(0,0,0,.2);
  bottom:0;
  height:auto;
  left:0;
  position:absolute;
  right:0;
  top:4rem;
  -webkit-transition: -webkit-box-shadow .5s ease-in-out;
  transition: -webkit-box-shadow .5s ease-in-out;
  -o-transition: box-shadow .5s ease-in-out;
  transition: box-shadow .5s ease-in-out;
  transition: box-shadow .5s ease-in-out, -webkit-box-shadow .5s ease-in-out;
}

.keywords--chart--wrapper h3 {
  display: block;
  /* font-family: 'Playfair Display', Serif; */
  font-size: 1.2rem;
  /* font-style: italic; */
  font-weight: 400;
  left: 50%;
  line-height: 1.5;
  margin: 0;
  overflow-wrap: break-word;
  position: absolute;
  text-align: center;
  text-transform: uppercase;
  bottom: .5rem;
  -webkit-transform: translate3d(-50%,0,0);
          transform: translate3d(-50%,0,0);
  white-space: nowrap;
  word-break: break-word;
  z-index:9;
}

@media screen and (min-width:750px) {
  .keywords--chart {
    margin-bottom:6rem;
  }
  .keywords--chart .keywords--chart--wrapper {
    top:6rem;
  }
}

@media screen and (min-width:1200px) {
  .keywords--chart {
    height: 50vh;
    margin: 0 0 8rem 0;
    padding-top: 8rem;
    top:0;
  }
}

.keywords[data-step="-1"] .keywords--chart .keywords--chart--wrapper {
  -webkit-box-shadow: none;
          box-shadow: none;
}

.keywords--chart--in {
  display:block;
  height:100%;
  position:relative;
  width:100%;
}

/*
 * Chart overrides
 */


 .keywords .keywords--chart .line svg .election-day--label:last-of-type {
   display: none;
 }

 @media screen and (min-width:750px) {
  .keywords .keywords--chart .line svg .election-day--label:last-of-type {
    display: block;
  }
 }

 .keywords .keywords--chart .line svg .series .series--serie path {
  stroke: #ededed;
  stroke-width: .1rem;
  -webkit-transition: stroke .5s ease-in-out, stroke-width .5s ease-in-out;
  -o-transition: stroke .5s ease-in-out, stroke-width .5s ease-in-out;
  transition: stroke .5s ease-in-out, stroke-width .5s ease-in-out;
  z-index:1;
 }
 
 .keywords[data-step="0"] .keywords--chart .line svg .series .series--serie path,
 .keywords[data-step="1"] .keywords--chart .line svg .series .series--serie path,
 .keywords[data-step="2"] .keywords--chart .line svg .series .series--serie path,
 .keywords[data-step="3"] .keywords--chart .line svg .series .series--serie path,
 .keywords[data-step="4"] .keywords--chart .line svg .series .series--serie path {
   stroke: #dcdcdc;
 }

.keywords[data-step="0"] .keywords--chart .line svg .series .series--serie.serie-0 path {
  stroke: #99d8c9;
  stroke-width: .3rem;
  z-index:2;
}

.keywords[data-step="1"] .keywords--chart .line svg .series .series--serie.serie-1 path {
  stroke: #2ca25f;
  stroke-width: .3rem;
  z-index:2;
}

.keywords[data-step="2"] .keywords--chart .line svg .series .series--serie.serie-2 path {
  stroke: #99d8c9;
  stroke-width: .3rem;
  z-index:2;
}

.keywords[data-step="3"] .keywords--chart .line svg .series .series--serie.serie-3 path {
  stroke: #2ca25f;
  stroke-width: .3rem;
  z-index:2;
}

.keywords[data-step="4"] .keywords--chart .line svg .series .series--serie.serie-4 path {
  stroke: #99d8c9;
  stroke-width: .3rem;
  z-index:2;
}


