.chart-matrix {
	display:block;
	height:auto;
	width:auto;
}

	.chart-matrix-table-wrapper {
		display:block;
		height:auto;
		margin: 0 auto;
		max-width: 100%;
		overflow-x: auto;
		overflow-y:hidden;
		width:auto;
	}
	
		.chart-matrix table {
			background:transparent;
			border: 0;
			border-spacing: 0;
			border-collapse: collapse;
			height:auto;
			margin:0 auto;
			width:auto;
		}
		
		.chart-matrix th {
			font-size:1rem;
			font-weight:300;
			padding:0;
			position:relative;
			text-transform: uppercase;
			white-space: nowrap;
		}
		
		.chart-matrix thead th {
			height:10rem;
			width:2rem;
		}
		
			.chart-matrix thead th div {
				bottom:-1.5rem;
				display:block;
				height:2rem;
				left:0;
				line-height:2rem;
				padding-left:.5rem;
				position:absolute;
				text-align:left;
				-webkit-transform: rotate(-90deg);
				    -ms-transform: rotate(-90deg);
				        transform: rotate(-90deg);
				-webkit-transform-origin: top left;
				    -ms-transform-origin: top left;
				        transform-origin: top left;
				width:10rem;
			}
		
		.chart-matrix tbody th {
			height:2rem;
			line-height:2rem;
			padding-right:.5rem;
			text-align:right;
			width:10rem;
		}
		
		.chart-matrix td {
			border:1px solid #fff;
			height:2rem;
			padding:0;
			width:2rem;
		}
		
		.chart-matrix td div {
			display:block;
			height:2rem;
			padding:0;
			width:2rem;
		}
		
			.chart-matrix .cell.cell-level-self div {
				background:#fff;
			}
		
			.chart-matrix .cell.cell-level-empty div {
				background:#edf8fb;
				opacity:.3;
			}
		
			.chart-matrix .cell.cell-level-lowest div {
				background:#ccece6;
			}
		
			.chart-matrix .cell.cell-level-low div {
				background:#99d8c9;
			}
		
			.chart-matrix .cell.cell-level-medium div {
				background:#66c2a4;
			}
		
			.chart-matrix .cell.cell-level-high div {
				background:#2ca25f;
			}
		
			.chart-matrix .cell.cell-level-highest div {
				background:#006d2c;
			}
			
		.chart-matrix .chart-matrix-legend {
			margin:2rem auto 0 auto;
			
		}
			
			.chart-matrix .chart-matrix-legend td {
				font-size: 1.2rem;
				font-weight: 300;
				height:1.5rem;
				text-align: center;
				vertical-align: middle;
				width:1.5rem;
			}
			
			.chart-matrix .chart-matrix-legend td div {
				height:1.5rem;
				width:1.5rem;
			}


