@-webkit-keyframes red-rect {
  0% {
    fill: #D0021B;
  }

  40% {
    fill: #4A90E2;
  }

  50% {
    fill: #F55F23;
  }

  60% {
    fill: #F8E71C;
  }

  100% {
    fill: #D0021B;
  }
}

@keyframes red-rect {
  0% {
    fill: #D0021B;
  }

  40% {
    fill: #4A90E2;
  }

  50% {
    fill: #F55F23;
  }

  60% {
    fill: #F8E71C;
  }

  100% {
    fill: #D0021B;
  }
}

@-webkit-keyframes green-rect {
  0% {
    fill: #7ED321;
  }

  40% {
    fill: #4A90E2;
  }

  50% {
    fill: #F55F23;
  }

  60% {
    fill: #F8E71C;
  }
  
  100% {
	  fill:#7ED321;
  }
}

@keyframes green-rect {
  0% {
    fill: #7ED321;
  }

  40% {
    fill: #4A90E2;
  }

  50% {
    fill: #F55F23;
  }

  60% {
    fill: #F8E71C;
  }
  
  100% {
	  fill:#7ED321;
  }
}

.loading {
	background:#fff;
	bottom:0;
	display:none;
	height:auto;
	left:0;
	opacity:0;
	position:fixed;
	right:0;
	top:0;
	-webkit-transform:translate3d(0,0,0);
	        transform:translate3d(0,0,0);
	-webkit-transition: all .25s ease-in-out;
	-o-transition: all .25s ease-in-out;
	transition: all .25s ease-in-out;
	width:auto;
	z-index:-1;
}

	.loading h2 {
		color:#333;
		display:block;
		height: 10rem;
		left:50%;
		position:absolute;
		top:50%;
		-webkit-transform: translate3d(-50%,-50%,0);
		        transform: translate3d(-50%,-50%,0);
		width:10rem;
	}
	
	@media screen and (min-width:750px) {
		.loading h2 {
			height:20rem;
			width:20rem;
		}
	}

.loading-visible .loading {
	display:block;
	z-index:10000;
}

.loading-visible.loading-in .loading {
	opacity:1;
	-webkit-transform:translate3d(0,0,0);
	        transform:translate3d(0,0,0);
}

#loading-logo-small {
	display:block;
	height:100%;
	width:100%;
}

	#loading-logo-small #loading-logo-small-red {
		-webkit-animation-direction: alternate;
		        animation-direction: alternate;
		-webkit-animation-duration: 3s;
		        animation-duration: 3s;
		-webkit-animation-iteration-count: infinite;
		        animation-iteration-count: infinite;
		-webkit-animation-name: red-rect;
		        animation-name: red-rect;
		
		fill:#D0021B;
		mix-blend-mode: multiply;
		position:absolute;
	}

	#loading-logo-small #loading-logo-small-green {
		-webkit-animation-direction: alternate;
		        animation-direction: alternate;
		-webkit-animation-duration: 3s;
		        animation-duration: 3s;
		-webkit-animation-iteration-count: infinite;
		        animation-iteration-count: infinite;
		-webkit-animation-name: green-rect;
		        animation-name: green-rect;
		
		fill:#7ED321;
		mix-blend-mode: multiply;
	}


