.timeline {
	margin-bottom: 4rem;
	max-width: 120rem;
}

@media screen and (min-width: 750px) {
	.timeline {
		margin-bottom: 6rem;
	}
}

@media screen and (min-width: 1020px) {
	.timeline {
		margin-bottom: 8rem;
	}
}

.timeline--data {
	margin: 0 -4rem;
}

@media screen and (min-width: 1020px) {
	.timeline--data {
		margin: 0;
	}
}

.timeline--page--viz {
	display: block;
	position: relative;
	width: 100%;
}

.timeline--page--viz--milestones {
	bottom:0;
	display:block;
	height:auto;
	left:0;
	position:absolute;
	right:0;
	top:0;
	width:auto;
	z-index:1;
}

.timeline--page--viz--articles {
	bottom:0;
	display:block;
	height:auto;
	left:0;
	position:absolute;
	top:0;
	width:17.5rem;
	z-index:3;
}



.timeline-page--viz--svg {
	bottom:0;
	display:block;
	height:auto;
	left:0;
	mix-blend-mode:multiply;
	position:absolute;
	top:0;
	width:auto;
	z-index:1;
}


.timeline-page--milestones--milestone,
.timeline-page--articles--article {
	display:block;
	height:11rem;
	left:0;
	position:absolute;
	width:17.5rem;
}

.timeline-page--milestones--milestone {
	display:block;
	height:auto;
	left:19.5rem;
	opacity:1;
	right:2rem;
	text-shadow:0 0 .1rem #fff;
	-webkit-transform:translate3d(0,-50%,0);
	        transform:translate3d(0,-50%,0);
	-webkit-transition:all .25s ease-in-out;
	-o-transition:all .25s ease-in-out;
	transition:all .25s ease-in-out;
	width:auto;
	z-index:999;
}

	.timeline-page--milestones--milestone.timeline-page--milestones--milestone--position-2 {
		left:33rem;
	}
	
	.timeline-page--milestones--milestone.hover {
		opacity:1;
	}

.timeline-page--milestones--milestone h2,
.timeline-page--articles--article h4,
.timeline-page--articles--article h2 {
	font-size:1.2rem;
	line-height:1.2;
	margin:0;
	padding:0 .5rem;
	text-align: right;
}

	.timeline-page--milestones--milestone h2 {
		font-size:3.6rem;
		font-style:italic;
		font-weight:900;
	}

.timeline-page--milestones--milestone h4,
.timeline-page--milestones--milestone h2 {
	text-align:center;
}

.timeline-page--milestones--milestone h4,
.timeline-page--articles--article h4 {
	font-size:1rem;
	line-height:1.2;
	margin-bottom:.5rem;
}

.timeline-page--milestones--milestone h2,
.timeline-page--articles--article h2 {
	font-family: 'Playfair Display', serif;
	font-weight:700;
	text-transform: capitalize;
}

.timeline-page--milestones--milestone h2 a,
.timeline-page--articles--article h2 a {
	color:#454545;
	text-decoration: none;
}

.timeline-page--articles--article h2 a:active,
.timeline-page--articles--article h2 a:hover {
	text-decoration: underline;
}

.timeline-page--articles--article--collections,
.timeline-page--articles--article--collections li {
	list-style: none;
	margin:0;
	padding:0;
	text-align: right;
}

.timeline-page--articles--article--collections li {
	display:inline-block;
	height:.8rem;
	margin-right:.5rem;
	position:relative;
	width:.8rem;
}

.timeline-page--articles--article--collections li a {
	display:inline-block;
}

.timeline-page--articles--article--collections li a:before {
    background: #eee;
    border-radius: 0;
    -webkit-box-shadow:0 0 0 .2rem #fefefe;
            box-shadow:0 0 0 .2rem #fefefe;
    content: "";
    display: block;
    height: .8rem;
    left: 0;
    position: absolute;
    top: 0;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-transform-origin: 0 0;
        -ms-transform-origin: 0 0;
            transform-origin: 0 0;
    width: .8rem;
}

.timeline-page--articles--article--collections li a span {
	display:none;
}

.timeline-page--articles--article--collections li a.destra:before {
	background:#000;
}

.timeline-page--articles--article--collections li a.centro-destra:before {
	background:#4A90E2;
}

.timeline-page--articles--article--collections li a.m5s:before {
	background:#F8E71C;
}

.timeline-page--articles--article--collections li a.centro-sinistra:before {
	background:#F55F23;
}

.timeline-page--articles--article--collections li a.sinistra:before {
	background:#D0021B;
}

.timeline-page--viz--svg--milestone-hover,
.timeline-page--viz--svg--milestone-line,
.timeline-page--viz--svg--date-line {
	stroke:#454545;
	stroke-dasharray: 1, 5;
	stroke-width: .1rem;
	z-index:99;
}

.timeline-page--viz--svg--milestone-line {
	stroke:rgba(69,69,69,1);
	stroke-dasharray:5, 1;
	stroke-width: .1rem;
}


.timeline-page--viz--svg--milestone-hover {
	stroke:rgba(255,69,69,0);
	stroke-dasharray: 0;
	stroke-width: 2rem;
}

.timeline-page--viz--svg--curve-activity {
	fill: #efefef;
    mix-blend-mode: multiply;
 }

.timeline-page--viz--svg--curve-popularity {
	fill: #ccc;
    mix-blend-mode: multiply;
 }
 
 
.destra .timeline-page--viz--svg--curve-popularity {
	fill: #ccc;
} 
.centro-destra .timeline-page--viz--svg--curve-popularity {
	fill: #4A90E2;
} 
.m5s .timeline-page--viz--svg--curve-popularity {
	fill: #F8E71C;
} 
.centro-sinistra .timeline-page--viz--svg--curve-popularity {
	fill: #F55F23;
} 
.sinistra .timeline-page--viz--svg--curve-popularity {
	fill: #D0021B;
}

.timeline--page--viz--legend {
	display:block;
	left:0;
	position:absolute;
	right:0;
	top:2rem;
	white-space: nowrap;
}

.timeline--page--viz--legend .party-title {
	display:block;
	position:absolute;
    text-align: center;
	top:0;
	-webkit-transform: translate3d(-50%,0,0);
	        transform: translate3d(-50%,0,0);
	z-index:3;
}

.timeline--page--viz--legend .party-title.articles {
	left:0;
	-webkit-transform: none;
	    -ms-transform: none;
	        transform: none;
	width:17.5rem;
}

.timeline--page--viz--legend h2 {
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.4rem;
    margin:0;
	text-shadow: 0 0 .2rem #fff;
    text-transform: uppercase;
    white-space: nowrap;
}

.timeline--page--viz--legend .party-title.articles h2 {
	white-space: normal;
}

.timeline--page--viz--legend span {
	display:inline-block;
	font-size: 1rem;
    font-weight: 400;
    height:1.4rem;
    line-height: 1.4rem;
    margin-right:.5rem;
    padding-left:1.4rem;
    position:relative;
	text-shadow: 0 0 .2rem #fff;
}

.timeline--page--viz--legend span::before {
	background:#efefef;
	border-radius:50%;
	-webkit-box-shadow: 0 0 0 .2rem #fff;
	        box-shadow: 0 0 0 .2rem #fff;
	content:"";
	display:block;
	height:1rem;
	left:0;
	mix-blend-mode: multiply;
	position:absolute;
	top:.2rem;
	width:1rem;
}

.timeline--page--viz--legend .destra span.popularity::before {
	background:#ccc;
}

.timeline--page--viz--legend .centro-destra span.popularity::before {
	background:#4A90E2;
}

.timeline--page--viz--legend .m5s span.popularity::before {
	background:#F8E71C;
}

.timeline--page--viz--legend .centro-sinistra span.popularity::before {
	background:#F55F23;
}

.timeline--page--viz--legend .sinistra span.popularity::before {
	background:#D0021B;
}

.timeline-page .stats {
	margin-bottom:0 0 4rem 0; 	
	padding:0;
}

	@media screen and (min-width: 750px) {
		.timeline-page .stats {
			margin-bottom:0 0 6rem 0; 	
			padding:0;
		}
	}

.timeline-page .stats .color {
	background:#efefef;
	border-radius:50%;
	content:"";
	display:inline-block;
	height:1rem;
	width:1rem;
	
}

.timeline-page .stats .color.destra {
	background:#ccc;
}

.timeline-page .stats .color.centro-destra {
	background:#4A90E2;
}

.timeline-page .stats .color.m5s {
	background:#F8E71C;
}

.timeline-page .stats .color.centro-sinistra {
	background:#F55F23;
}

.timeline-page .stats .color.sinistra {
	background:#D0021B;
}


