@-webkit-keyframes header-red-rect {
  0% {
    fill: #D0021B;
  }
  
  40% {
    fill: #D0021B;
  }

  45% {
    fill: #4A90E2;
  }

  50% {
    fill: #F55F23;
  }

  55% {
    fill: #F8E71C;
  }

  60% {
    fill: #D0021B;
  }

  100% {
    fill: #D0021B;
  }
}

@keyframes header-red-rect {
  0% {
    fill: #D0021B;
  }
  
  40% {
    fill: #D0021B;
  }

  45% {
    fill: #4A90E2;
  }

  50% {
    fill: #F55F23;
  }

  55% {
    fill: #F8E71C;
  }

  60% {
    fill: #D0021B;
  }

  100% {
    fill: #D0021B;
  }
}

@-webkit-keyframes header-green-rect {
  0% {
    fill: #7ED321;
  }
  
  40% {
    fill: #7ED321;
  }

  45% {
    fill: #4A90E2;
  }

  50% {
    fill: #F55F23;
  }

  55% {
    fill: #F8E71C;
  }
  
  60% {
    fill: #7ED321;
  }
  
  100% {
	  fill:#7ED321;
  }
}

@keyframes header-green-rect {
  0% {
    fill: #7ED321;
  }
  
  40% {
    fill: #7ED321;
  }

  45% {
    fill: #4A90E2;
  }

  50% {
    fill: #F55F23;
  }

  55% {
    fill: #F8E71C;
  }
  
  60% {
    fill: #7ED321;
  }
  
  100% {
	  fill:#7ED321;
  }
