.main-header {
	background:#fff;
	-webkit-box-shadow: 0 0 5rem 1rem rgba(200,200,200,.1);
	        box-shadow: 0 0 5rem 1rem rgba(200,200,200,.1);
	display:block;
	height:4rem;
	line-height: 4rem;
	left:0;
	position:fixed;
	right:0;
	top:0;
	z-index:9999;
}
	
	@media screen and (min-width:750px) {
		.main-header {
			height:6rem;
			line-height:6rem;
		}
	}
	
	@media screen and (min-width:1200px) {
		.main-header {
			background:transparent;
			-webkit-box-shadow: none;
			        box-shadow: none;
			height:15rem;
			line-height:15rem;
		}
	}

	.main-header h1 {
		display:block;
		font-family: 'Playfair Display', serif;
		font-size:1.6rem;
		font-style:italic;
		font-weight: 700;
		height:4rem;
		line-height: 4rem;
		margin:0 4rem;
		overflow:hidden;
		padding:0;
		text-align:center;
		-o-text-overflow:ellipsis;
		   text-overflow:ellipsis;
	}
	
		.main-header h1 #header-logo-small {
			display:none;
			height:4rem;
			width:4rem;
		}
		
		#header-logo-small #header-logo-small-red {
			-webkit-animation-direction: alternate;
			        animation-direction: alternate;
			-webkit-animation-duration: 30s;
			        animation-duration: 30s;
			-webkit-animation-iteration-count: infinite;
			        animation-iteration-count: infinite;
			-webkit-animation-name: header-red-rect;
			        animation-name: header-red-rect;
			
			fill:#D0021B;
			mix-blend-mode: multiply;
			position:absolute;
		}

		#header-logo-small #header-logo-small-green {
			-webkit-animation-direction: alternate;
			        animation-direction: alternate;
			-webkit-animation-duration: 30s;
			        animation-duration: 30s;
			-webkit-animation-iteration-count: infinite;
			        animation-iteration-count: infinite;
			-webkit-animation-name: header-green-rect;
			        animation-name: header-green-rect;
			
			fill:#7ED321;
			mix-blend-mode: multiply;
		}
	
		@media screen and (min-width:750px) {
			.main-header h1 {
				font-size:2rem;
				height:6rem;
				line-height:6rem;
				margin-left:4rem;
				text-align:left;
			}
			
			.main-header h1 #header-logo-small {
				display:inline-block;
				height:6rem;
				margin-right:2rem;
				vertical-align:middle;
				width:6rem;
			}
		}
		
		@media screen and (min-width:1200px) {
			.main-header h1 {
				font-size:3.6rem;
				height:15rem;
				line-height:15rem;
			}
			
			.main-header h1 #header-logo-small {
				display:inline-block;
				height:8rem;
				margin-right:2rem;
				vertical-align:middle;
				width:8rem;
			}
		}
	
		.main-header h1 a {
			color:#333;
			text-decoration:none;
		}
		
		.main-header h1 a:active,
		.main-header h1 a:hover {
			color:#333;
			text-decoration:underline;
		}
		
		.main-header h1 span {
			font-style: italic;
			font-weight:400;
			text-transform: capitalize;
		}
		
#nav-toggle {
	display:block;
	height:4rem;
	position:absolute;
	right:1.3rem;
	top:0;
	width:4rem;
}
	
	@media screen and (min-width:750px) {
		#nav-toggle {
			right:3.3rem;
			top:1rem;
		}
	}
	
	@media screen and (min-width:1200px) {
		#nav-toggle {
			display: none;
			top:5.5rem;
		}
	}


	#nav-toggle .bottom,
	#nav-toggle .middle,
	#nav-toggle .top {
		background:#333;
		display:block;
		height:.1rem;
		left:.7rem;
		position:absolute;
		top:1.2rem;
		-webkit-transform-origin:center center;
		    -ms-transform-origin:center center;
		        transform-origin:center center;
		-webkit-transform:rotate(0deg);
		    -ms-transform:rotate(0deg);
		        transform:rotate(0deg);
		-webkit-transition: all .25s ease-in-out;
		-o-transition: all .25s ease-in-out;
		transition: all .25s ease-in-out;
		width: 2.6rem;
	}
	
	#nav-toggle .bottom {
		bottom:1.2rem;
		top:auto;
	}
	
	#nav-toggle .middle {
		top:2rem;
	}

.nav-visible #nav-toggle .top {
	top:2rem;
}
.nav-visible #nav-toggle .bottom {
	bottom:2rem;
}
	
.nav-visible.nav-in #nav-toggle .top {
	-webkit-transform:rotate(45deg);
	    -ms-transform:rotate(45deg);
	        transform:rotate(45deg);
}
.nav-visible.nav-in #nav-toggle .bottom {
	-webkit-transform:rotate(-45deg);
	    -ms-transform:rotate(-45deg);
	        transform:rotate(-45deg);
}
.nav-visible #nav-toggle .middle {
	opacity:0;
}


