.chart-scatter {
	display:block;
	height:auto;
	width:auto;
}

	.chart-scatter svg.chart-scatter-viz {
		display:block;
		height:30rem;
		width:100%;
	}
	
	@media screen and (min-width:750px) {
		.chart-scatter svg.chart-scatter-viz {
			height:40rem;
		}
	}
	
	.chart-scatter svg.chart-scatter-viz .axis {
		stroke:#666;
		stroke-width:.1rem;
	}
	
	.chart-scatter svg.chart-scatter-viz .arrow {
		fill:#666;
		stroke:#666;
		stroke-width:.1rem;
	}
	
	.chart-scatter svg.chart-scatter-viz .avg {
		mix-blend-mode:multiply;
		stroke:#999;
		stroke-dasharray: 1, 2;
		stroke-width:.1rem;
	}
	
	.chart-scatter svg.chart-scatter-viz .regression {
		mix-blend-mode:multiply;
		stroke:#66c2a4;
		stroke-dasharray: 2, 2;
		stroke-width:.2rem;
	}
	
	.chart-scatter svg.chart-scatter-viz .label {
		color:#666;
		font-size:1rem;
		letter-spacing:.1rem;
		text-transform: uppercase;
	}
	
		.chart-scatter svg.chart-scatter-viz .label-avg {
			color:#999;
			font-size:1rem;
			text-transform: uppercase;
		}
	
	.chart-scatter svg.chart-scatter-viz .point-group .scatter-point {
		fill:#66c2a4;
		mix-blend-mode:multiply;
		-webkit-transition: all .25s ease-in-out;
		-o-transition: all .25s ease-in-out;
		transition: all .25s ease-in-out;
	}
	
		.chart-scatter svg.chart-scatter-viz .point-group:hover .scatter-point,
		.chart-scatter svg.chart-scatter-viz .point-group:active .scatter-point {
			fill:#006d2c;
		}
		
	.chart-scatter svg.chart-scatter-viz .point-group .label {
		fill:#333;
		font-size:1.2rem;
		font-weight:400;
		opacity:0;
		text-align:center;
		text-transform: uppercase;
		-webkit-transition: all .25s ease-in-out;
		-o-transition: all .25s ease-in-out;
		transition: all .25s ease-in-out;
	}
	
		.chart-scatter svg.chart-scatter-viz .point-group:hover .label,
		.chart-scatter svg.chart-scatter-viz .point-group:active .label {
			opacity:1;
		}


