.items-wrapper .items {
	list-style: none;
	margin:0;
	padding:0;
}

	@media screen and (min-width:750px) {
		.items-wrapper .items {
			-ms-flex-line-pack:stretch;
			    align-content:stretch;
			-ms-flex-align:start;
			    align-items:flex-start;
			display:-ms-flexbox;
			display:flex;
			-ms-flex-direction:row;
			    flex-direction:row;
			-ms-flex-wrap:wrap;
			    flex-wrap:wrap;
			-ms-flex-pack:start;
			    justify-content:flex-start;
		}
	}

.items-wrapper .items-coverage {
	margin-bottom:4rem;
}

	@media screen and (min-width:750px) {
		.items-wrapper .items-coverage {
			margin:0 2rem 6rem 2rem;
		}
	}

	@media screen and (min-width:1360px) {
		.items-wrapper .items-coverage {
			margin:0 3rem 6rem 3rem;
		}
	}

.pagination-wrapper {
	display:block;
	height:5rem;
	width:100%;
}

.pagination-wrapper .pagination {
	list-style: none;
	height:5rem;
	margin:0;
	padding:0;
	position:relative;
	width:100%;
}

.pagination-wrapper .pagination li {
	display:block;
	height:5rem;
	line-height:5rem;
	list-style: none;
	position:absolute;
	top:0;
	width:auto;
}

.pagination-wrapper .pagination li a {
	color:#333;
	display:block;
	font-family: 'Playfair Display', Serif;
	font-size: 1.6rem;
	font-style:italic;
	font-weight: 700;
	height:5rem;
	line-height:5rem;
	text-decoration: none;
}

.pagination-wrapper .pagination .pagination-prev {
	left:0;
	right:50%;
	text-align:left;
}

	.pagination-wrapper .pagination .pagination-prev a::before {
		content:"❮ ";
	}

.pagination-wrapper .pagination .pagination-next {
	left:50%;
	right:0;
	text-align:right;
}

	.pagination-wrapper .pagination .pagination-next a::after {
		content:" ❯";
	}


