@-webkit-keyframes stats-red-rect {
  0% {
    fill: #D0021B;
  }

  40% {
    fill: #4A90E2;
  }

  50% {
    fill: #F55F23;
  }

  60% {
    fill: #F8E71C;
  }

  100% {
    fill: #D0021B;
  }
}

@keyframes stats-red-rect {
  0% {
    fill: #D0021B;
  }

  40% {
    fill: #4A90E2;
  }

  50% {
    fill: #F55F23;
  }

  60% {
    fill: #F8E71C;
  }

  100% {
    fill: #D0021B;
  }
}

@-webkit-keyframes stats-green-rect {
  0% {
    fill: #7ED321;
  }

  40% {
    fill: #4A90E2;
  }

  50% {
    fill: #F55F23;
  }

  60% {
    fill: #F8E71C;
  }
  
  100% {
	  fill:#7ED321;
  }
}

@keyframes stats-green-rect {
  0% {
    fill: #7ED321;
  }

  40% {
    fill: #4A90E2;
  }

  50% {
    fill: #F55F23;
  }

  60% {
    fill: #F8E71C;
  }
  
  100% {
	  fill:#7ED321;
  }
}

.stats-logo-small {
	height:7.7rem;
	left:50%;
	position:absolute;
	top:50%;
	-webkit-transform: translate3d(-50%,-50%,0);
	        transform: translate3d(-50%,-50%,0);
	width:15rem;
	z-index:1;
}
	
.stats-logo-small.stats-logo-small-green,
.stats-logo-small .stats-logo-small-red {
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
	-webkit-animation-duration: 3s;
	        animation-duration: 3s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-name: stats-red-rect;
	        animation-name: stats-red-rect;
	
	fill:#D0021B;
	mix-blend-mode: multiply;
	position:absolute;
}

.stats-logo-small .stats-logo-small-green {
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
	-webkit-animation-duration: 3s;
	        animation-duration: 3s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-name: stats-green-rect;
	        animation-name: stats-green-rect;
	
	fill:#7ED321;
	mix-blend-mode: multiply;
}


