.text {
	max-width:86rem;
	padding:0;
}

	@media screen and (min-width:750px) {
		.text {
			padding:0 2rem;
		}
	}

	@media screen and (min-width:1360px) {
		.text {
			padding:0 3rem;
		}
	}

.text .text-title {
	color:#333;
	display: block;
	font-family: 'Playfair Display', Serif;
	font-size: 1.8rem;
	font-style: italic;
	font-weight: 700;
	line-height:1.5;
	margin:0 0 2rem 0;
	overflow-wrap: break-word;
	padding-right:4rem;
	text-transform: none;
	word-break: break-word;
}

	@media screen and (min-width:750px) {
		.text .text-title {
			font-size:2rem;
		}
	}

.text .text-body p {
	display: block;
	font-family: 'Playfair Display', Serif;
	font-size: 1.4rem;
	font-weight: 400;
	line-height:1.5;
	margin:0 0 2rem 0;
	overflow-wrap: break-word;
	text-transform: none;
	word-break: break-word;	
}

	@media screen and (min-width:750px) {
		.text .text-body p {
			font-size:1.6rem;
		}
	}

.text .text-body h2 {
	color:#333;
	display: block;
	font-family: 'Playfair Display', Serif;
	font-size: 1.4rem;
	font-style: italic;
	font-weight: 700;
	line-height:1.5;
	margin:0 0 1rem 0;
	overflow-wrap: break-word;
	padding-right:4rem;
	text-transform: none;
	word-break: break-word;
}

	@media screen and (min-width:750px) {
		.text .text-body h2 {
			font-size:1.6rem;
			margin-bottom:1.5rem;
		}
	}

.text .text-body h3 {
	color:#333;
	font-size:1.4rem;
	font-weight:400;
	letter-spacing:.1rem;
	line-height:1.5;
	margin:0 0 0 0;
	overflow-wrap: break-word;
	padding-right:4rem;
	text-decoration: none;
	text-transform: uppercase;
	word-break: break-word;
}

	@media screen and (min-width:750px) {
		.text .text-body h3 {
			font-size:1.6rem;
		}
	}
	
.text .text-body a {
	color:#333;
	text-decoration: underline;
}

.text .text-body p + h2,
.text .text-body dl + h2,
.text .text-body ol + h2,
.text .text-body ul + h2 {
	margin-top:2rem;
}

	@media screen and (min-width:750px) {
		.text .text-body p + h2,
		.text .text-body dl + h2,
		.text .text-body ol + h2,
		.text .text-body ul + h2 {
			margin-top:4rem;
		}
	}

.text .text-body p.img {
	margin-bottom:2rem;
	margin-top:4rem;
	text-align:center;
}

	.text .text-body p.img img {
		height:auto;
		max-width:100%;
		width:auto;
	}
	
.text .text-body dl {
	display:block;
	margin:0 0 2rem 0;
}

	.text .text-body dl dt {
		display:inline-block;
		font-size:1.2rem;
		font-weight:300;
		letter-spacing: .1rem;
		line-height:2;
		padding-right:1rem;
		text-transform: uppercase;
	}

	.text .text-body dl dd {
		display:block;
		font-size:1.4rem;
		font-weight:400;
		letter-spacing: .1rem;
		line-height:2;
		margin:0;
		padding-right:1rem;
	}


