.results {
  margin-bottom: 4rem;
}
.results--legend {
  max-width: 120rem;
}

.results .l {
  display: none;
}

@media screen and (min-width:750px) {
  .results {
    margin-bottom: 6rem;
  }

  .results .s {
    display: none;
  }

  .results .l {
    display: inline-block;
  }
}

@media screen and (min-width:1200px) {
  .results {
    margin-bottom: 8rem;
  }
}

.results--chart {
  max-width: 120rem;
}

.results--chart--wrapper {
  display: block;
  height: auto;
  margin-top: 2rem;
  width: 100%;
}

.results--chart--in {
  display: block;
  height: auto;
  width: 100%;
}

.results--chart--body {
  height: 35rem;
  padding: 0 1rem;
  position: relative;
  width: 100%;
}

.results--chart--container {
  height: 100%;
  position: relative;
  width: 100%;
}

@media screen and (min-width:750px) {
  .results--chart--wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    margin-top: 6rem;
  }

  .results--chart--in {
    display: block;
    -ms-flex: 0 0 20%;
        flex: 0 0 20%;
    width: auto;
  }
}

.results--chart--header h2 {
  display: block;
  font-family: 'Playfair Display', Serif;
  font-size: 1.6rem;
  font-style: italic;
  font-weight: 700;
  line-height: 1.5;
  margin: 0 0 2rem;
  overflow-wrap: break-word;
  text-align: center;
  text-transform: none;
  white-space: nowrap;
  word-break: break-word;
}

@media screen and (min-width:750px) {
  .results--chart--header h2 {
    font-size: 1.2rem;
  }
}

@media screen and (min-width:1020px) {
  .results--chart--header h2 {
    font-size: 1.6rem;
  }
}

.results--chart--footer {
  padding: 0 1rem;
}

.results--chart--footer p {
  color: #999;
  font-size: 1.2rem;
  text-align: center;
}


